.about-container {
    padding: 10px 10vw 0vw 10vw;
    .row {
        margin-bottom: 20px;
    }
    a {
        display: inline;
    }
}

.about-header {
    color: white;
    padding: 35px 10vw 10vw 10vw;
}

.full-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-header-container {
    background-color: #0b5ed7;
    width: 100%;
    height: 100px;
}

.team-container {
    margin-top: 30px;;
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(16deg);
    }
    20% {
        transform: rotate(-6deg);
    }
    30% {
        transform: rotate(16deg);
    }
    40% {
        transform: rotate(-4deg);
    }
    50% {
        transform: rotate(16deg);
    }
    60% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

.waving-hand {
    animation: wave 2.1s 0.6s infinite;
    transform-origin: 75% 75%;
    display: inline-block;
}


@keyframes fly {
    0% {
        top: 130%;
        left: 130%;
        transform: translate(-130%, -130%);
    }
    50% {
        transform: rotate(16deg);
    }
}

@keyframes flyer {
    0% {
        top: -130%;
        left: -130%;
        transform: translate(130%, 130%);
    }
    50% {
        transform: rotate(16deg);
    }
}

.rocket {
    animation: fly 4.1s 0.6s 1;
    position: absolute;
    top: -220px;
    left: 0;
    width: 400px;
}

.rocket2 {
    animation: flyer 4.1s 0.6s 1;
    position: absolute;
    top: -220px;
    left: 0;
    width: 400px;
}